.setup {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-lg);
}

.setup-card {
  max-width: 400px;
  width: 100%;
  text-align: center;
  padding: var(--space-2xl);
}

.setup-icon {
  font-size: 3rem;
  margin-bottom: var(--space-md);
}

.setup-title {
  font-family: var(--font-display);
  font-size: 1.75rem;
  margin-bottom: var(--space-sm);
}

.setup-description {
  color: var(--color-text-soft);
  margin-bottom: var(--space-xl);
}

.setup-message {
  margin-top: var(--space-lg);
  padding: var(--space-md);
  border-radius: var(--radius-md);
  font-size: 0.9rem;
}

.setup-message.success {
  background: #f0fff4;
  color: #276749;
}

.setup-message.error {
  background: #fff5f5;
  color: #c53030;
}

.setup-message.info {
  background: #ebf8ff;
  color: #2b6cb0;
}

.setup-next {
  margin-top: var(--space-lg);
  margin-bottom: 0;
}

.setup-next a {
  color: var(--color-primary-dark);
  font-weight: 500;
}